.login-p {
    background-image: url('~/public/images/bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    .logo {
        width: 160px;
        height: 111.7px;
        margin: 80px auto 20px auto;
    }
    .license {
        font-size: 12px;
        font-weight: 600;
        line-height: 16px;
        text-align: center;
        color: #888888;
        width: 209px;
        margin: 48px 0 40px 0;
    }
}
.login-p .section-auth {
    background: #fff;
    border-radius: 4px;
    width: 312px;
    min-height: 144px;
    position: relative;
    margin: 0 0 116px 0;
    .MuiTabPanel-root {
        padding: 0 14px;
    }
    .MuiTabs-indicator {
        display: none;
    }
    .MuiTab-textColorPrimary {
        color: #888888;
        background: #F4F4F4;
        border-radius: 4px 4px 0 0;
        flex: 1;
        &.Mui-selected {
            color: #FFAB00;
            background: #FFFFFF;
            box-shadow: 1px -4px 4px rgb(0 0 0 / 16%);
            position: relative;
            z-index: 2;
        }
        &:nth-of-type(1) {
            border-radius: 4px 4px 4px 0;
        }
        &:nth-of-type(2) {
            border-radius: 4px 4px 0 4px;
        }
    }
    .MuiInput-root {
        height: 48px;
        width: 100%;
        &, &:hover {
            &:before, &:after {
                border-bottom: 1px solid #E0E0E0;
            }
        }
    }
    .login-btn {
        font-weight: 600;
        height: 44px;
        width: 100%;
        background: #FFAB00;
        border-radius: 4px;
        position: absolute;
        bottom: -68px;
        left: 0;
    }
    .forgot-pwd-btn {
        font-weight: 600;
        color: #fff;
        position: absolute;
        bottom: -116px;
        left: 0;
        text-transform: none;
        width: 100%;
        height: 24px;
    }
}